import React from "react";
import "../styles/Loader.css";
import loader from "../assets/audio.svg"

const Loader = ({loading}) => {
  return(
    <>
      {
        loading 
        ? <div className="visible">
            <img src={loader} alt="CARGANDO..." />
          </div>
        : <div className="no-visible"></div>
      }
    </>
  )
}

export default Loader;