import React,{useState, useEffect} from "react";
import SearcherStyles from "../styles/Searcher.module.css";

const Searcher = ({handleSearch}) => {
  
  const initialData = {
    artistName: "",
    artistSong: "",
  }

  const [data,setData] = useState(initialData);

  const dating = (e) => {
    e.preventDefault();
    handleSearch(data);
    setData(initialData);
  }

  const handleChange = e => {
    
    let value = e.target.value,
      name = e.target.name;
    let newData = {
      ...data,
      [name] : value
    }

    setData(newData);

  }

  return (
    <>
      <form 
        className={SearcherStyles.form}
        onSubmit={e => dating(e)}
        >
        <p>Search song: </p>
        <input 
        type="text" 
        name="artistName" 
        id="artistName"
        placeholder="Insert artist's name"
        onChange={e => handleChange(e)}
        value={data.artistName}
        required
        />
        <input 
        type="text" 
        name="artistSong" 
        id="artistSong"
        placeholder="Insert artist's song"
        onChange={e => handleChange(e)}
        value={data.artistSong}
        required
        />
        <input 
        type="submit" 
        value="FIND" 
        />
      </form>
    </>
  )
}

export default Searcher;