import React, { useEffect, useState } from "react";
import LyricsStyles from "../styles/Lyrics.module.css";

const Lyrics = ({ search, song, lyric, message }) => {

  const [letra, setLetra] = useState(lyric);

  useEffect(() => {
    if (!lyric) return;

    setLetra(lyric);

  }, [lyric]);

  const puttingTheLyric = () => {
    const container = document.querySelector(".lyricsFormated");
    container.innerHTML = letra;
  };

  return (
    <div className={LyricsStyles.zone}>
      <p>{search ? `${search.artistName} - ${search.artistSong}` : ""}</p>
      <p>{song ? `Song: ${song || ""}` : ""}</p>
      <div className="lyricsFormated">
        {
        lyric 
        ? puttingTheLyric() 
        : message 
          ? message
          : "WAITING..."
        }
      </div>
    </div>
  );
};

export default Lyrics;
