import React, {useState, useEffect} from "react";
import RootStyles from "./styles/Root.module.css";
import Searcher from "./components/Searcher.js";
import Lyrics from "./components/Lyrics.js";
import Loader from "./components/Loader.js";
import CreateCard from "./components/CreateCard.js";
import {helpHttp} from "./Helpers/helpHttp.js";
import "./styles/root.css"

const Root = () => {
  
  const [search,setSearch] = useState("");
  const [song,setSong] = useState(null);
  const [lyric,setLyric] = useState(null);
  const [loading,setLoading] = useState(false);
  const [message, setMessage] = useState("HERE I WAIT FOR THE LYRICS");

  const handleSearch = (data) => {

    if(!data) return;

    console.log("En handleSearch",data);
    setSearch(data);
    setSong(data.artistSong);
    /* 
    console.log(document.getSelection().toString());
    Esta funciÃƒÂ³n regresa el texto que estÃƒÂ¡ siendo seleccionado
    */

  }

  useEffect(() => {
    
    if(!search) return;

    
    const fetching = async () => {
      
      setLoading(true);
      
      const {artistName, artistSong} = search;

      const artistUrl = `https://theaudiodb.com/api/v1/json/2/search.php?s=${artistName}`;
      const lyricUrl = `https://api.lyrics.ovh/v1/${artistName}/${artistSong}`;

      const [artistRes, lyricRes] = await Promise.all([
        helpHttp().get(artistUrl),
        helpHttp().get(lyricUrl)
      ]);

      if(lyricRes.err || !lyricRes.hasOwnProperty("lyrics")){
        console.log(lyricRes);
        setMessage("There's an error. Try again");
        setLoading(false);
        setLyric("");
        return
      }

      if(artistRes.err){
        setMessage("The artist isn´t in the data base");
        setLoading(false);
        setLyric("");
        return
      }

      console.log(artistRes)
      console.log(lyricRes)

       const ready = reset(lyricRes.lyrics);
       
       setLyric(ready);

       setLoading(false);

    }

    const reset = (lyrics) => {
      const res = lyrics.replace(/[\n\r]/g,"<br/>");

      return res;
    }

    fetching();

  },[search])

  const createAPic = (e) => {
    let selection = document.getSelection().toString();
    console.log(selection)
    console.log(e);
  }

  return (
  <div className={RootStyles.rootStyles}>
    <Loader loading={loading} />
    <div className={RootStyles.headerContainer}>
      <header>
        <h1>Stared Lyrics</h1>
      </header>
      <nav>
        <ul className={RootStyles.rrssList}>
          <li>
            <a 
              href="https://www.linkedin.com/in/adonis-mendoza-4a7696197/"
              target="_blank"
              rel="noreferrer noopener"
            >
              Linkdn
            </a>
          </li>
          <li>
            <a 
            href="https://github.com/donisaurius" 
            target="_blank" rel="noopener noreferrer">
            Github
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <div className={RootStyles.actionZone}>
      <div className={RootStyles.BuscadorStyles}>
        <Searcher 
          handleSearch={handleSearch} 
        />
      </div>
      <div className={RootStyles.lyricZone}>
        <Lyrics 
          search={search} 
          song={song} 
          lyric={lyric}
          message={message}
        />
        {/* <CreateCard createAPic={createAPic} /> */}
      </div>
    </div>
    <div className={RootStyles.squaretostyle}></div>
  </div>)
}

export default Root;